* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montseratt', sans-serif;
}

.App {
  background-position: bottom;
  margin: 0 auto;
  border-radius: 30px;
}

.App.clouds {
  background-image: url('./assets/clouds.gif');
}
.App.rain {
  background-image: url('./assets/rain.gif');
}
.App.clear {
  background-image: url('./assets/sun.gif');
}
.App.snow {
  background-image: url('./assets/snow.gif');
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(58, 29, 225, 0.664), rgba(19, 85, 207, 0.429));

  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 50px 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 16px;
  margin-top: -25px;

  box-shadow: rgba(0, 0, 0, 0.2);
  color: #313131;
  font-size: 20px;

  transition: 0.4 ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.weather-container {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 30px;
  padding: 15px 0 25px 0;
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  padding: 25px 0;
  text-shadow: 3px, 3px, rgba(50, 50, 50, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px, 2px, rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
  margin: 0 50px;
}

.weather-box .temp {
  position: relative;
  display: inline-block;

  border-radius: 16px;
  padding: 15px, 25px;
  color: #fff;
  font-size: 92px;
  font-weight: 900;
  text-shadow: 3px, 3px, rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px, 3px, rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px, 3px, rgba(50, 50, 70, 0.5);
}
.weather-icon {
  margin-top: 15px;
}

.error {
  text-align: center;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 30px;
  padding: 15px 0 25px 0;
  color: #fff;
  font-size: 25px;
  font-weight: 700;
}
